import React, { Component } from 'react';
import ghDiffHTML from 'gh-diff-html';
import Textarea from 'muicss/lib/react/textarea';
import * as prefixNgClass from "posthtml-prefix-ngclass/dist/posthtml-prefix-ngclass.frontend";

import './App.css';
import 'muicss/dist/css/mui.css';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            beforeHtml: '',
            afterHtml: ''
        };
    }

    createMarkup = () => {
        return {__html: ghDiffHTML(this.state.beforeHtml, this.state.afterHtml, {
            fileName: 'test.html',
            outputFormat: 'side-by-side'
        })};
    };

    onChange = (event) => {
        this.setState({
            beforeHtml: event.target.value,
            afterHtml: prefixNgClass.set(event.target.value, {prefix: "prefix-"})
        });
    };

    render() {
      return (
        <div className="App">
            <h1>Demo <a href={"https://github.com/loymax/posthtml-prefix-ngclass"} target={"_blank"}>posthtml-prefix-ngclass</a></h1>
            <h2>Input</h2>
            <Textarea
                label="Insert HTML"
                floatingLabel={true}
                value={this.state.beforeHtml}
                onChange={this.onChange}
                rows={10}
            />
            <h2>Output</h2>
            {!this.state.beforeHtml && <p>Please, insert HTML in a form</p>}
            {this.state.beforeHtml && <div className="content" dangerouslySetInnerHTML={this.createMarkup()} />}
        </div>
      );
    }
}

export default App;



